html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  @include zero-edges;
  @include column-flex;

  font-family: sans-serif;
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;

  min-height: 100vh;
  background-color: $lcg-1;
  color: $dcg-1;

  h1 {
    font-size: 25px;
    line-height: 1.2;
    font-weight: 700;
    color: inherit;

    margin-top: 1em;
    margin-bottom: 0.75em;
    text-align: center;
  }
}

.visually-hidden {
  @include visually-hidden;
}

.list {
  @include zero-edges;
  list-style: none;
}

.visible {
  animation-name: show;
  animation-duration: 0.5s;
  animation-timing-function: ease;
}

@keyframes show {
  from {
    opacity: 0.4;
  }

  to {
    opacity: 1;
  }
}
