.slider {
  width: 60%;
  min-width: 320px;
  max-width: 1000px; // ширина самого узкого изображения
  position: relative;
  margin-bottom: 2.5em;

  .slider__tip > p {
    display: none;
  }

  .slider__list {
    display: flex;
    width: 100%;
    outline: none;
    list-style: none;
    box-shadow: 0 0 10px $dcg-3;
    transition: box-shadow 0.2s ease;

    overflow-x: auto;
    scroll-snap-type: x mandatory;

    &:focus,
    &:hover {
      box-shadow: 0 0 10px $dcg-1;
    }

    &:focus ~ .slider__tips .slider__tip--focus > p,
    &:hover ~ .slider__tips .slider__tip--hover > p {
      display: block;
    }

    &:hover ~ .slider__tips .slider__tip--focus > p {
      display: none;
    }

    &:focus:hover ~ .slider__tips .slider__tip--focus > p,
    &:focus:hover ~ .slider__tips .slider__tip--hover > p {
      display: none;
    }

    &:focus:hover ~ .slider__tips .slider__tip--focus-and-hover > p {
      display: block;
    }
  }

  .slider__item {
    flex-basis: 100%;
    flex-shrink: 0;
    margin-right: 1px;

    scroll-snap-align: start;
    scroll-snap-stop: always;
  }

  figure {
    @include zero-edges;
    @include column-flex;
    width: 100%;
  }

  .padded-box {
    @include padded-box;
    padding-bottom: $ratio-large;
    margin-bottom: 0.5em;
  }

  img {
    @include content-in-padded-box;
  }

  figcaption {
    font-style: italic;
    padding-bottom: 0.75em;
  }

  .slider__controls {
    position: absolute;
  }

  .slider__dots {
    top: 0;
    left: 0;

    display: flex;
    align-items: flex-start;
    padding-top: 0.5em;
    padding-left: 0.5em;
    line-height: 0;
  }

  .slider__dot {
    &:not(:last-child) {
      margin-right: 0.4em;
    }
  }

  .slider__dot-btn {
    width: 1em;
    height: 1em;
    border: 1px solid $lcg-1;
    border-radius: 50%;
    outline: none;
    background-color: transparent;
    cursor: pointer;

    @include zero-edges;

    &.active {
      background-color: $lcg-1;
    }

    &:focus,
    &:hover {
      box-shadow: 0 0 5px $lcg-1;
    }
  }

